import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AdmindashboardComponent } from './admindashboard/admindashboard.component';
import { ProductListComponent } from './product/product-list.component';
import { ProductCreateComponent } from './product/product-create.component';
import { CategoryListComponent } from './category/category-list.component';
import { CategoryCreateComponent } from './category/category-create.component';
import { AdminlayoutComponent } from './shared/adminlayout.component';

const adminRoutes: Routes = [
    {
        path: '', component: AdminlayoutComponent, children: [
            { path: '', component: AdmindashboardComponent },
            { path: 'product', component: ProductListComponent },
            { path: 'product/create', component: ProductCreateComponent },
            { path: 'category', component: CategoryListComponent },
            { path: 'category/create', component: CategoryCreateComponent }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(adminRoutes)],
    exports: [RouterModule],
})
export class AdminRoutingModule { }

export const routedComponents = [AdminlayoutComponent,AdmindashboardComponent, ProductListComponent, ProductCreateComponent, CategoryListComponent, CategoryCreateComponent];


